import Vue from 'vue'
import VueRouter from 'vue-router'
import client_report from '../components/client_report.vue'
import CompareReport from '../components/CompareReport.vue'
import home from '../components/home.vue'
import login from '../components/login.vue'
import Unity_report from '../components/Unity_report.vue'
import Unity_detail from '../components/Unity_detail.vue'
import Summary from '../components/UnityData/Summary.vue'
import CPU_function from '../components/UnityData/CPU_function.vue'
import CPU_performace from '../components/UnityData/CPU_performace.vue'
import Hard_ele from '../components/UnityData/Hard_ele.vue'
import Hard_GPU from '../components/UnityData/Hard_GPU.vue'
import Memory_consume from '../components/UnityData/Memory_consume.vue'
import Memory_resource from '../components/UnityData/Memory_resource.vue'
import Rendering_summary from '../components/UnityData/Rendering_summary.vue'
import Rendering_Batch from '../components/UnityData/Rendering_Batch.vue'
import AdReport from '../components/AdaptiveReport/SumReport.vue'
import SaReport from '../components/SafeReport/SumReport.vue'
import Sadetail from '../components/SafeReport/Sadetail.vue'
import StReport from '../components/StressReport/SumReport.vue'

import Vuecookies from 'vue-cookies'

Vue.use(VueRouter)
// 兼容IE报错
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location){
  return originalPush.call(this,location).catch(err=>err);
}

  const routes = [
  {
      path: '/',
      redirect: '/login'
  },
  {
    path: '/login',
    component: login
  },
  {
    path: '/home',
    component: home,
    redirect: '/client_report',
    children: [
        { path: '/client_report', component: client_report },
        { path: '/Unity_report', component: Unity_report },
        { path: '/AdReport', component: AdReport },
        { path: '/SaReport', component: SaReport },
        { path: '/StReport', component: StReport },
   
    ]
  },
  {
    path: '/Unity_detail',
    component: Unity_detail,
    redirect: '/Summary',
    children: [
        { path: '/Summary', component: Summary },
        { path: '/CPU_function', component: CPU_function },
        { path: '/CPU_performace', component: CPU_performace },
        { path: '/Hard_ele', component: Hard_ele },
        { path: '/Hard_GPU', component: Hard_GPU },
        { path: '/Memory_consume', component: Memory_consume },
        { path: '/Memory_resource', component: Memory_resource },
        { path: '/Rendering_summary', component: Rendering_summary },
        { path: '/Rendering_Batch', component: Rendering_Batch },
    ]
  },
  {
    path: '/Compare',
    component: CompareReport
  },
  {
    path: '/Sadetail',
    component: Sadetail
  },

]

const router = new VueRouter({
  mode: 'history',
  routes
})

//挂载路由导航守卫
router.beforeEach((to, from, next) => {
  //     //to 将要访问的路劲
  //     //from 代表从哪个路径跳转出来
  //     //next 是一个行数，表示放行
  //     //  next（）放行   next('/login') 强制跳转
      if (to.path == '/login') return next();
  //     //获取token值
      const tokenStr = Vuecookies.get('token');
      //const sessionid = Vuecookies.get("SESSIONID");
      //console.log(tokenStr)
      if (!tokenStr) return next('/login') //正式环境修改
      // if (!sessionid) return next('/login')
      // if (!tokenStr) return next()
      next()
  })
  

export default router
