<template>
        <el-main>
          <el-card>
            <!-- 筛选栏 -->
            <div class="summary" v-for="(item0,index0) in divList" :key="index0">
              <span >选择项目</span>
              <el-select v-model="item0.setSelectInfo.Project"  clearable placeholder="选择项目" @change="selectProject1(index0)">
                <el-option
                  v-for="(item,index) in options1" :key="index" :label="item.game_name" :value="item.game_name"> </el-option>
              </el-select>-
              <el-select v-model="item0.setSelectInfo.VersionId"  clearable placeholder="选择版本" @change="selectProject1(index0)">
                <el-option
                  v-for="(item,index) in options1" :key="index" :label="item.game_name" :value="item.game_name"> </el-option>
              </el-select>
              <el-button type="primary" style="margin-left:100px" @click="reset">重置</el-button>  
            </div>
            <!-- 测试详情 -->
            <template>
              <el-table ref="reportTable" :data="ProjectInfo" style="width: 100%;cursor:pointer" highlight-current-row   @row-click="getReport" >
                <!-- <el-table-column type="index" label="序号"></el-table-column> -->
                <el-table-column  label="项目" width="auto"> 
                  <div slot-scope="scope" class="project">
                    <!-- {{scope.row}} -->
                    <div  style="display:contents">
                      <img  style="width:52px;height:52px" :src="scope.row.imgUrl" />
                      <span style="margin-left:10px ;font-weight:bold;color:blue" @click="Report(scope.row)">{{scope.row.Name}}</span>
                    </div>
                  </div>
                </el-table-column>
                <el-table-column prop="Version" label="版本"  width="auto"></el-table-column>
                <el-table-column  prop="Date" label="测试日期" width="auto"></el-table-column>                                         -->
              </el-table>
            </template>
            <!-- 页码区-->
            <el-pagination @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="tableData.length"
                :page-size="10"
                ></el-pagination>
          </el-card>  
        </el-main>
</template>
<script>
export default {
  data(){
return {  
        //筛选栏增添
        divList:[
           {setSelectInfo:{
            Project: '',
            VersionId:'',
            platform:"",
           }},
        ], 
        setData:{
          data:[]
        },
        //自研项目列表 
        options1: [],
        optionsVersion1:[],
        optionData:[],
        // 选中数据
        // 修改项目信息
        setGameData:{},
        // 测试数据
        tableData: [],
        dialogVisable:false,
        dialogVisable1:false,
        gameType:["a","b","c"],
        pageInfo: {
          // 当前的页数
          pagenum: 1,
          // 当前每页显示多少条数据
          pagesize: 8, 
          total:3,
          currentData:[],
        },
        
        ProjectInfo:[{
          imgUrl:"http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png",
          Name:'小猫爱消除',
          Version:'1.0.0',
          Date:'2020-4-8',
          id:1
        }],
      }

  },
  created(){
    this.getProjectList();
    // this.getfilter();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
     //重置筛选数据
    reset(){
      this.divList=[
        { setSelectInfo:{
          Project: '',
          VersionId:'',
          platform:"",
        }},
      ], 
      this.Selection1=[];
      this.optionsVersion1=[];
      this.optionsVersion2=[],
      this.getRecentProject();
    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.tableData.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },

    // 请求初始化信息
    async getProjectList(){
      const {data:res} = await this.$http.get("init_select_list")
      .catch((error) => {
          console.log('错误信息:'+error)
          }
      );
      // console.log(res)
      this.options1= res.data.dict.self_list;
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
    },
    getReport(val){     

      let id = val.id
      var {href} = this.$router.resolve({
        path:'/Sadetail',
        query:{
          id:id
        }
      });
      window.open(href)
    },
  }
}
</script>

