<template>
        <el-main>
          <el-card>
            <!-- 筛选栏 -->
            <div class="summary" v-for="(item0,index0) in divList" :key="index0">
              <span >选择项目</span>
              <el-select v-model="item0.setSelectInfo.Project"  clearable placeholder="选择项目" @change="selectProject1(index0)">
                <el-option
                  v-for="(item,index) in options1" :key="index" :label="item.game_name" :value="item.game_name"> </el-option>
              </el-select>-
              <el-select v-model="item0.setSelectInfo.VersionId"  clearable placeholder="选择版本" @change="selectProject1(index0)">
                <el-option
                  v-for="(item,index) in options1" :key="index" :label="item.game_name" :value="item.game_name"> </el-option>
              </el-select>
              <el-button type="primary" style="margin-left:100px" @click="reset">重置</el-button>  
            </div>
            <div  style="margin:40px 0 80px 10px">
              <div> <img  style="height:160px;width:160px;margin-right:20px;float:left" :src="ProjectInfo.imgUrl" /></div>
              <div>
                <h1>项目名：{{ProjectInfo.ProjectName}} v_{{ProjectInfo.ProjectVersion}}</h1> 
                <p>包名：{{ProjectInfo.bundleid}}</p> 
                <p>Unity版本：{{ProjectInfo.UnityVersion}}</p> 
              </div>
            </div>
            <el-divider></el-divider>
            <div v-for="(item,index) in UnityTestinfo" :key="index" style="margin-bottom:30px;border-bottom:1px solid #eee">
              <div style="display:flex; width:90%;padding-left:20px ">
                <div style="padding-top:10px;width:40%">
                  <p>测试日期:{{item.testDate}}</p> 
                  <p> 测试时长:{{item.testTime}}</p> 
                </div>
                <div style="width:40%">
                  <p>测试机型:{{item.testDevice}}</p> 
                  <p>CPU频率:{{item.deviceCPU}}</p> 
                  <p>内存：{{item.deviceMemory}}</p> 
                </div>
                <el-button type="text" style="font-size:15px;margin-right:25px;float:right;" @click="open_Unity_detail(item.id)">查看报告</el-button>
              </div>
            </div>
            <!-- 页码区-->
            <el-pagination @current-change="handleCurrentChange" @prev-click="prevPage" @next-click="nextPage" :total="tableData.length"
                :page-size="10"
                ></el-pagination>
          </el-card>  
        </el-main>
</template>
<script>
export default {
  data(){
return {  
        //筛选栏增添
        divList:[
           {setSelectInfo:{
            Project: '',
            VersionId:'',
            platform:"",
           }},
        ], 
        setData:{
          data:[]
        },
        //自研项目列表 
        options1: [],
        optionsVersion1:[],
        optionData:[],
        // 选中数据
        // 修改项目信息
        setGameData:{},
        // 测试数据
        tableData: [],
        dialogVisable:false,
        dialogVisable1:false,
        gameType:["a","b","c"],
        pageInfo: {
          // 当前的页数
          pagenum: 1,
          // 当前每页显示多少条数据
          pagesize: 8, 
          total:3,
          currentData:[],
        },
        
        ProjectInfo:{
          imgUrl:"http://10.0.10.246/icon/%E5%B0%8F%E7%8C%AB%E7%88%B1%E6%B6%88%E9%99%A4.png",
          ProjectName:'小猫爱消除',
          ProjectVersion:'1.0.0',
          bundleid:'com.skymoons.kittenmatch',
          UnityVersion:'2017.4'
        },
        UnityTestinfo:[{
          module:"游戏页面",
          testDate:'2020-8-30',
          testTime:'00:01:30',
          testDevice:'xiaomi 8',
          deviceCPU:'2.8Ghz(8核)',
          deviceMemory:'6G',
          id:1
        },
        {
          module:"大厅",
          testDate:'2020-8-30',
          testTime:'00:01:30',
          testDevice:'xiaomi 8',
          deviceCPU:'2.8Ghz(8核)',
          deviceMemory:'6G',
          id:2
        }]
        
      }

  },
  created(){
    this.getProjectList();
    // this.getfilter();
  },
  computed:{
    upData() {
      return{
        body:JSON.stringify(this.setGameData)
      }
    },
  },
  methods:{
     //重置筛选数据
    reset(){
      this.divList=[
        { setSelectInfo:{
          Project: '',
          VersionId:'',
          platform:"",
        }},
      ], 
      this.Selection1=[];
      this.optionsVersion1=[];
      this.optionsVersion2=[],
      this.getRecentProject();
    },
    // 设置当前页面数据--分页使用
    setCurrentPageData(){
      let begin = (this.pageInfo.pagenum-1)*this.pageInfo.pagesize;
      let end = this.pageInfo.pagenum *this.pageInfo.pagesize;
      this.pageInfo.currentData = this.tableData.slice(begin,end); 
    },
    handleCurrentChange(newPage) {
      this.pageInfo.pagenum = newPage;
      this.setCurrentPageData();
		},
    // 上一页
    prevPage(){
      if(this.pageInfo.pagenum ==1) return;
      this.pageInfo.pagenum--;
      this.setCurrentPageData();
    },
    // 下一页
    nextPage(){
      if(this.pageInfo.pagenum == this.pageInfo.total) return;
      this.pageInfo.pagenum++;
      this.setCurrentPageData();
    },

    // 请求初始化信息
    async getProjectList(){
      const {data:res} = await this.$http.get("init_select_list")
      .catch((error) => {
          console.log('错误信息:'+error)
          }
      );
      // console.log(res)
      this.options1= res.data.dict.self_list;
      if(res.meta.status !==200){
        this.$message.error(res.meta.msg)
      }
    },
    // 获取展示页面所有项目的id，并组成列表
    getidlist(){
      this.Selection2=[];
      const list = [];
      for(let i=0;i<this.pageInfo.currentData.length;i++){
        list.push(this.pageInfo.currentData[i].id)
      }
      return list;
    },
    // 打开unity详情页
    open_Unity_detail(key){
      var {href} = this.$router.resolve({
        path:'/Unity_detail',
        query:{
          id:key
        }
      });
      window.open(href)
    }
  }
}
</script>

